<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Activity</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="red accent-3"
                            @click="Reset()">
                            <!-- <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon> -->
                            Reset Activity
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="activitys" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.id">
                                <td>{{ index + 1 }}</td>
                                <td>{{ item.name_user }}</td>
                                <td>{{ item.activity}}</td>
                                <td>{{item.created_at}}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
export default {
    data() {
        return {
            dialog: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                },
                {
                    text: 'Name',
                    value: 'name_user'
                },
                {
                    text: 'Aktivitas',
                    value: 'activity'
                },
                {
                    text: 'Tanggal',
                    value: 'created_at'
                },
            ],
            activitys: [],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            form: {
                name: '',
                username: '',
                password: ''
            },
            user: new FormData,
            typeInput: 'new',
            errors: '',
            updatedId: '',
        }
    },
    methods: {
        getData() {
            var uri = this.$apiUrl + 'activity'
            this.$http.get(uri).then(response => {
                this.activitys = response.data
            })
        },
        Reset() {
            var uri = this.$apiUrl + 'activity/reset'
            this.$http.post(uri).then(response => {
                this.getData();
            })
        },
    },
    mounted() {
        this.getData();
    },
}
</script>